<template>
  <div>
    <v-icon @click="LoadMembers">list</v-icon>
    <v-dialog v-model="dialog"
              max-width="1200px">
      <v-card>
        <v-card-title>
          <v-layout align-start
                    justify-space-between
                    row
                    fill-height>
            <v-flex xs12>
              <div class="pl-1">
                <v-progress-circular indeterminate
                                     color="primary"
                                     class="mt-3 ml-0"
                                     v-if="loading">
                </v-progress-circular>

                <exportMenu v-if="adGroupMembers.length > 0"
                            :data="adGroupMembers"
                            :headers="headers"></exportMenu>
              </div>
            </v-flex>
            <v-flex xs12>
              <v-text-field v-model="search"
                            prepend-icon="filter_list"
                            label="Filter results"
                            single-line
                            hide-details>
              </v-text-field>
            </v-flex>
          </v-layout>
        </v-card-title>

        <v-data-table :headers="headers"
                      :items="adGroupMembers"
                      :loading=false
                      item-key="id"
                      :search="search"
                      :rows-per-page-items="rowsperpage"
                      class="px-2">

          <v-progress-linear slot="progress"
                             color="primary"
                             indeterminate
                             height="5">
          </v-progress-linear>

          <template slot="items"
                    slot-scope="props">
            <tr>
              <td class="text-xs-left pl-3">{{ props.item.name }}</td>
              <td class="text-xs-left pl-3">{{ props.item.description }}</td>
              <td class="text-xs-left pl-3">{{ props.item.distinguishedName }}</td>
            </tr>
          </template>
        </v-data-table>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import exportMenu from "../../components/exportMenu";
import { FindGroupMembers } from "@/services/api/ActiveDirectory";
import { Notify } from "@/services/HelperService";
import { DataTableMixin } from "@/components/Mixins/DataTableMixin";

export default {
  mixins: [DataTableMixin],
  components: {
    exportMenu
  },
  props: ["data", "domain"],
  data() {
    return {
      adGroupMembers: [],
      dialog: false,
      search: "",
      headers: [
        {
          text: "Name",
          align: "left",
          sortable: true,
          value: "name",
          class: "pl-3"
        },
        {
          text: "Description",
          align: "left",
          sortable: true,
          value: "description",
          class: "pl-3"
        },
        {
          text: "DistinguishedName",
          align: "left",
          class: "pl-3",
          value: "distinguishedName"
        }
      ]
    };
  },
  methods: {
    GetGroupMembers() {
      this.loading = true;
      FindGroupMembers({ UserGroup: this.data, Domain: this.domain })
        .then(data => {
          this.adGroupMembers = data;
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    },
    LoadMembers() {
      this.adUsersGroups = [];
      this.dialog = !this.dialog;
      this.GetGroupMembers();
    }
  }
};
</script>
