<template>
  <section class="checkFeeds pt-2 mx-3">
    <page-intro :introName="introName"></page-intro>
    <v-card>
      <v-card-title>
        <v-layout align-start
                  justify-space-between
                  row
                  fill-height>
          <v-flex xs6
                  class="pr-3">
            <v-text-field append-icon="search"
                          label="Find Groups..."
                          v-model="lookup"
                          single-line
                          required
                          @input="$v.lookup.$touch()"
                          @blur="$v.lookup.$touch()"
                          @keyup.enter="GetADGroup()"
                          :error-messages="nameErrors">
            </v-text-field>
          </v-flex>
          <v-flex xs3>
            <v-select v-model="select"
                      :items="items"
                      :error-messages="selectErrors"
                      label="Domain"
                      required
                      @change="$v.select.$touch()"
                      @blur="$v.select.$touch()">
            </v-select>
          </v-flex>
          <v-flex xs12>
            <div class="pl-5">
              <v-progress-circular indeterminate
                                   color="primary"
                                   class="mt-3 ml-0"
                                   v-if="loading">
              </v-progress-circular>

              <v-btn :disabled="this.$v.$invalid"
                     v-if="!loading"
                     color="tfgprimary"
                     class="mt-3 ml-0"
                     round
                     small
                     :dark="!this.$v.$invalid"
                     @click="GetADGroup()">
                Find
              </v-btn>
              <exportMenu v-if="adGroups.length > 0"
                          :data="adGroups"
                          :headers="headersEdited"
                          :colStyles="colStyles"></exportMenu>
            </div>
          </v-flex>
          <v-flex xs12>
            <v-text-field v-model="search"
                          prepend-icon="filter_list"
                          label="Filter results"
                          single-line
                          hide-details>
            </v-text-field>
          </v-flex>
        </v-layout>
      </v-card-title>

      <v-data-table :headers="headers"
                    :items="adGroups"
                    item-key="id"
                    :search="search"
                    :rows-per-page-items="rowsperpage"
                    class="px-2">

        <v-progress-linear slot="progress"
                           color="primary"
                           indeterminate
                           height="5">
        </v-progress-linear>

        <template slot="items"
                  slot-scope="props">
          <tr>
            <td class="text-xs-left pl-3">{{ props.item.name }}</td>
            <td class="text-xs-left pl-3">{{ props.item.description }}</td>
            <td class="text-xs-left pl-3">{{ props.item.distinguishedName }}</td>
            <td class="text-xs-center px-1">{{ props.item.groupScope }}</td>
            <td class="text-xs-center px-1">
              <GroupMembers :data="props.item.name"
                            :domain="select"></GroupMembers>
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-card>
  </section>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength } from "vuelidate/lib/validators";
import { FindADGroups } from "@/services/api/ActiveDirectory";
import { Notify } from "@/services/HelperService";
import exportMenu from "@/components/exportMenu";
import PageIntro from "@/components/PageIntro.vue";
import GroupMembers from "@/components/ActiveDirectory/GroupMembers";
import { DataTableMixin } from "@/components/Mixins/DataTableMixin";
import { AdVuelidateMixin } from "@/components/Mixins/AdVuelidateMixin";

export default {
  mixins: [validationMixin, DataTableMixin, AdVuelidateMixin],

  validations: {
    lookup: {
      required,
      minLength: minLength(4)
    },
    select: {
      required
    }
  },

  components: {
    exportMenu,
    GroupMembers,
    PageIntro
  },

  data() {
    return {
      introName: "Active Directory Find Groups and Members",
      adGroups: [],
      lookup: "",
      select: "HO",
      search: "",
      items: ["HO", "STORES", "TFGSTORES.local"],
      colStyles: {
        name: {
          columnWidth: 50
        },
        description: {
          columnWidth: 50
        },
        distinguishedName: {
          columnWidth: "auto"
        },
        groupScope: {
          columnWidth: 40
        }
      },
      headers: [
        {
          text: "Name",
          align: "center",
          sortable: true,
          value: "name",
          class: "pl-1"
        },
        {
          text: "Description",
          align: "center",
          sortable: true,
          value: "description",
          class: "pl-1"
        },
        {
          text: "DistinguishedName",
          align: "center",
          class: "pl-1",
          value: "distinguishedName"
        },
        {
          text: "GroupScope",
          align: "center",
          sortable: true,
          value: "groupScope",
          class: "pl-1"
        },
        {
          text: "Members",
          align: "center",
          sortable: true,
          value: "View"
        }
      ]
    };
  },

  computed: {
    headersEdited() {
      let customheaders = this.headers.slice();
      customheaders.pop();
      return customheaders;
    }
  },

  methods: {
    GetADGroup() {
      this.loading = true;
      FindADGroups({ UserGroup: this.lookup, Domain: this.select })
        .then(data => {
          this.adGroups = data;
        })
        .catch(error => {
          this.$notify(Notify(error));
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>
